<template>
  <div class="login_father">
    <div class="login_img">
      <div class="logo_link">
        <svg width="164" height="43" viewBox="0 0 164 43" fill="none" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
          <rect width="164" height="43" fill="url(#pattern0)"/>
          <defs>
            <pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1" height="1">
              <use xlink:href="#image0_5_44" transform="matrix(0.00101833 0 0 0.0039004 0 -0.00478423)"/>
            </pattern>
            <image id="image0_5_44" width="982" height="254" xlink:href="data:image/png;base64,"/>
          </defs>
        </svg>

      </div>
        <div class="login_text">
          Dastur orqali ish samaradorligizni
          oshiring va ishlash tizimingizni takomillashtiring
        </div>
      <div class="login_text_main">
        <div class="login_title">Dasturning dastlabki bosqichi</div>
        <div class="login_descr">Dasturni takomillashtirish ustida ishlar olib borilmoqda </div>
      </div>
    </div>
    <div class="login_form">
      <div>
          <div class="login_form">
            <div class="form_title">Dasturga kirish</div>
            <div class="form_desc">Login parolingizni kiritng</div>
          </div>
        <div class="form_f">
          <form @submit.prevent="login()">
            <div class="input_father">
              <input type="text" v-model="username" placeholder="Login">
            </div>
            <div class="input_father">
              <input type="password" v-model="password" placeholder="Password">
            </div>
            <div>
              <button class="login_btn">
                <b-spinner variant="white" v-if="isLoading"></b-spinner>
                <span v-if="!isLoading">Kirish</span>
                </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      username:'',
      password:'',
      isLoading:false
    }
  },
  methods:{
    login(){
      this.isLoading=true
      this.$axiosDefault.post('employee/login',{
        "username":this.username,
        "password":this.password
      })
      .then(res=>{
        this.isLoading=false
        localStorage.setItem('token',res.data.access)
        this.$router.push('/')
      })
      .catch(()=>{
        this.$toast("Login yoki parl xato", {
          timeout: 2000,
          type:"error"
        });
        this.isLoading=false
      })
    }
  }
}
</script>
